@import "../../global.scss";

.works {
  background-color: rgb(31, 31, 31);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  overflow: hidden;

    
  
    h1{
      padding-top: 2%;
    font-size: 50px;
    font-weight: 200;
    overflow: hidden;
    color:white;
      @include mobile{
        font-size: 20px;
      }
    }
    
    
  
  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;

    @include mobile {
      display: none;
    }

    &.left {
      left: 100px;
      transform: rotateY(180deg);
      padding-top: 18%;
      
      @include mobile {
        padding-top: 0%;
      }
    }

    &.right {
      right: 100px;
      padding-top: 18%;
      @include mobile {
        padding-top: 0%;
      }
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    padding-top: 10%;
    left: 0;
    transition: all 1s ease-out;

    @include mobile {
      height: 100vh;
      padding-top: 0%;
      flex-direction: column;
      justify-content: center;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;
        background-color: lightgray;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile {
          width: 80%;
          height: 150px;
          margin: 15px 0;
        }

        .left {
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: lightgreen ;
              display: flex;
              align-items: center;
              justify-content: center;

              @include mobile {
                width: 20px;
                height: 20px;
              }

              img {
                width: 25px;

                @include mobile {
                  width: 15px;
                }
              }
            }
            h2 {
              font-size: 20px;

              @include mobile{
                font-size: 13px;
              }
            }
            p {
              font-size: 13px;
              @include mobile{
                display: none;
              }
            }
            a {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              color:$mainColor;
              cursor: pointer;
            }
          }
        }
        .right {
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          background-color: lightgray;
          img {
            width: 400px;
            transform: rotate(-10deg);
          }
        }
      }
    }
  }
}