.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        position: relative;
        top:70px;
        scroll-behavior: smooth;
        background-color: lightgrey;
        scroll-snap-type: y mandatory;
        scrollbar-width: none;// for firefox
        &::-webkit-scrollbar{
            display:none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
    
}